const raymon = require('raymon-client');

class SocketService {
    static ip = window.location.hostname;
    //static ip = "37.152.176.100";
    static port = 9934;
    static client;

    static connect() {
        this.client = new raymon();
        this.client.connect(this.ip, this.port);
    }
}

export default SocketService;