<template>
    <div id="app">
        <NavbarComponent/>
        <LoginComponent v-if="!this.token" @setToken="setToken"/>
        <ChatComponent v-if="this.token" @setToken="setToken"/>
    </div>
</template>

<script>
    import ChatComponent from './components/ChatComponent.vue'
    import NavbarComponent from './components/NavbarComponent.vue'
    import LoginComponent from './components/LoginComponent.vue'

    export default {
        name: 'app',
        components: {
            NavbarComponent,
            LoginComponent,
            ChatComponent
        },
        data() {
            return {
                token: localStorage.getItem('user-token')
            }
        },
        created() {
        },
        methods: {
            setToken: function (t) {
                console.log(t);
                this.token = t;
            }
        }
    }
</script>

