<template>
    <!-- Default form login -->
    <form class="form" @submit.prevent="login">
        <p class="h4 text-center mb-4">Sign in</p>
        <label for="defaultFormLoginIdEx" class="grey-text">Your ID</label>
        <input :disabled="waiting" required v-model="username" type="text" id="defaultFormLoginIdEx"
               class="form-control"/>
        <br/>
        <label for="defaultFormLoginPasswordEx" class="grey-text">Your Password</label>
        <input :disabled="waiting" required v-model="password" type="password" id="defaultFormLoginPasswordEx"
               class="form-control"/>
        <div class="text-center mt-4">
            <b-button :disabled="waiting" type="submit">Login</b-button>
        </div>
        <p class="error" v-if="error">{{error}}</p>

    </form>
    <!-- Default form login -->
</template>

<script>
    import SocketService from "../services/SocketService";

    export default {
        name: 'LoginComponent',
        data() {
            return {
                username: '',
                password: '',
                error: '',
                waiting: false
            }
        },
        created() {
            console.log("login created")
        },
        methods: {
            login: function () {
                this.error = "";
                this.waiting = true;
                SocketService.connect();
                SocketService.client.handleEvent(SocketService.client.EventHandlers.OnConnect,
                    () => {
                        SocketService.client.login(this.username, this.password);
                        SocketService.client.handleEvent(SocketService.client.EventHandlers.OnLogin,
                            (token) => {
                                if (token == null) {
                                    this.waiting = false;
                                    this.error = "Username or password is invalid.";
                                } else {
                                    SocketService.client.disconnect();
                                    SocketService.client = null;

                                    localStorage.setItem('user-token', token);
                                    this.$emit("setToken", token);
                                }
                            })
                    });

                SocketService.client.handleEvent(SocketService.client.EventHandlers.OnDisconnect,
                    () => {
                        this.waiting = false;
                        this.error = "Check internet connection";
                    })
            }
        }
    }
</script>

<style scoped>
    .form {
        max-width: 400px;
        margin: 50px auto 10px auto;
        padding: 0px 50px 0px 50px;
    }

    p.error {
        border-radius: 5px;
        border: 1px solid #ff5b5f;
        background-color: #ffc5c1;
        padding: 10px;
        margin-top: 30px;
    }
</style>
